* {
  margin: 0;
  padding: 0
}

.comp {
  background-color: #faf9f8
}

.rbc {
  height: 85vh
}

.tabs {
  padding-top: 70px
}

.whatsnew,.wwb {
  height: 75vh;
  background-color: #321a2a
}

header .container-fluid {
  padding-left: 0;
  padding-right: 0
}

#rbcLogo {
  height: 50px
}

.dashboard {
  padding-top: 40px
}

.rbc .jumbotron {
  background: url('./resources/reformed.jpg') no-repeat 50%;
  background-size: cover
}

.rbc .jumbotron,.wwb .jumbotron {
  display: flex;
  height: 100%;
  overflow: hidden
}

.wwb .jumbotron {
  background: url('./resources/tulip2.jpg') no-repeat 50%;
  background-size: 95%
}

.leadership .jumbotron {
  margin-top: 80px;
  background: url('./resources/torch.jpg') no-repeat 50%;
  background-size: cover;
  display: flex;
  height: 400px;
  overflow: hidden
}

.whatsnew .jumbotron {
  background: url('./resources/faith.jpg') no-repeat 50%;
  background-size: 95%;
  display: flex;
  height: 100%;
  overflow: hidden
}

.jumbotron .jumbotron-content {
  display: flex;
  align-content: center;
  justify-content: flex-start;
  flex-direction: column;
  align-self: flex-end
}

.jumbotron .jumbotron-content h1 {
  font-size: 100px;
  color: #f8f9fa
}

.jumbotron .jumbotron-content p {
  padding-left: 5px;
  color: #f8f9fa
}

.sunday-gatherings {
  min-height: 75vh
}

.sunday-evening {
  margin-top: -10px;
  padding-top: 0px;
  font-size: 10px;
  font-style: italic;
}

.elders .row,.government .row,.mission .row,.our-beliefs .row,.welcome .row,.what-to-expect .row,.who-we-are .row {
  padding-top: 50px;
  display: flex
}

.events-row {
  padding-top: 50px
}

.login .row {
  padding-top: 40px;
  min-height: 92vh
}

.unauthorized .row {
  padding-top: 90px;
  min-height: 92vh
}

.unauthorized-button {
  margin-right: 5px
}

.give .row:last-child,.sermons .row:last-child,.sunday-gatherings .row:last-child,.welcome .row:last-child,.what-to-expect .row:last-child,.whatsnew .row:last-child {
  padding-bottom: 50px
}

.confessional,.covenantal,.deacon,.elder,.family,.ordinary,.reformed {
  padding-bottom: 10px
}

.give .row {
  padding-top: 80px;
  display: flex;
  min-height: 90vh
}

.solas {
  padding-top: 0!important
}

.contact img,.solas img,.statements img {
  width: 100%
}

.blockquote {
  margin-left: 50px
}

.contact,.leadership-spacer,.statements {
  margin-bottom: 50px
}

.statements li {
  margin-left: 30px
}

.statements li a {
  color: #333
}

.leadership-container {
  padding-bottom: 20px
}

.leadership-image {
  height: 100%;
  width: 100%
}

#mapid {
  height: 60vh;
  width: 100%
}

.locationInformation {
  padding-top: 50px
}

.locationInformation #serviceTimes {
  z-index: 801;
  background-color: #f8f9fa;
  opacity: .75
}

.locationInformation h3 {
  padding-top: 20px;
  font-weight: 700
}

#mapid hr {
  margin: 0 auto;
  width: 50%;
  padding-bottom: 20px
}

.locationInformation .street {
  margin-bottom: 0;
  padding-bottom: 0
}

.locationInformation #serviceTimes .study {
  padding-bottom: 2px;
  margin-bottom: 0
}

.locationInformation #serviceTimes div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center
}

.locationInformation #serviceTimes #getDirections {
  margin: 0 auto
}

.locationInformation #serviceTimes a {
  text-decoration: none;
  min-width: 150px;
  padding: .5em 1em;
  background-color: #555;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  outline: 0;
  border: none;
  cursor: pointer;
  font-style: normal;
  border-radius: 2em;
  letter-spacing: .02em
}

.sola {
  margin-right: 5px
}

.contactLink {
  text-decoration: none;
  color: #333
}

footer {
  left: 0;
  bottom: 0;
  background-color: #333;
  min-height: 80px;
  color: #999;
  z-index: 802
}

footer .col-md-2 {
  text-align: center;
  margin-top: 22.5px
}

footer #openContactModal {
  text-decoration: none;
  background: none;
  color: #999;
  border: none;
  padding: 0
}

footer #openContactModal:hover,footer a {
  color: #999
}

footer a {
  text-decoration: none
}

footer a:hover {
  color: #999
}

@media screen and (min-width: 707px) and (max-width:924px) {
  #mapid h3 {
      padding-top:20px;
      font-weight: 700;
      font-size: 20px
  }
}

@media screen and (max-width: 812px) {
  .jumbotron .jumbotron-content h1 {
      font-size:50px
  }

  .jumbotron .jumbotron-content p {
      font-size: 25px;
      color: #f8f9fa
  }

  #map iframe {
      width: 95%;
      padding-bottom: 20px
  }

  .what-to-expect .what-to-expect-list {
      padding-left: 3px
  }
}

@media screen and (max-width: 568px) {
  .jumbotron .jumbotron-content h1 {
      font-size:25px
  }

  .jumbotron .jumbotron-content p {
      font-size: 12px;
      color: #f8f9fa
  }

  #map iframe {
      height: 300px;
      width: 95%;
      padding-bottom: 20px
  }

  .what-to-expect .what-to-expect-list {
      padding-left: 3px;
      font-size: 12px
  }
}

#react-paginate ul {
  padding-left: 15px;
  padding-right: 15px
}

#react-paginate li,#react-paginate ul {
  display: inline-block
}
